import React, { useState } from "react";
import "../../../css/forms/fileUpload.css";
import axios from "axios";
const FileUpload = () => {
  const [selectedFile, setSelectedFile] = useState(null);

  const onChangeHandler = (event) => {
    //console.log(event.target.files[0]);   // single upload
    //setSelectedFile(event.target.files[0]); // single upload
    setSelectedFile(event.target.files);  // multiple upload
    console.log(event.target.files);
  };

  const onClickHandler = () => {
    const data = new FormData();
    //data.append("file", selectedFile); // single upload
    for(var x = 0; x < selectedFile.length; x++){
      data.append('file', selectedFile[x]);
    } 
    axios.post("http://localhost:3000/movie/upload", data, {
      // receive two    parameter endpoint url ,form data
    })
    .then(response => response.data)
    .then(data => {
        console.log(data);
    })
  };

  return (
    <div className="container">
      <div className="row">
        <div className="col-md-12">
          <form method="post" action="#" id="#">
            <div className="form-group files">
              <label>Upload Your File</label>
              <input
                type="file"
                className="form-control"
                multiple
                onChange={onChangeHandler}
              />
            </div>
            <button
              type="button"
              className="btn btn-success btn-block"
              onClick={onClickHandler}
            >
              Upload
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default FileUpload;
